.detail_section {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    min-height: 100vh;
    padding: 1rem;
}
/* todo  */

.button_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.backHome_btn {
    background-color: rgb(73, 73, 196);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-size: 1.1rem;
    letter-spacing: 1px;
    padding: 0.35rem 0.85rem;
    text-transform: capitalize;
    transition: 300ms ease-in;
}

.backHome_btn:hover {
    background-color: transparent;
    color: #4949c4;
    outline: 2px solid #4949c4;
    font-weight: 600;
}

.update_btn,
.delete_btn {
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.1rem;
    letter-spacing: 1px;
    padding: 0.35rem 0.85rem;
    text-transform: capitalize;
    transition: 300ms ease-in;
}

.update_btn {
    background-color: hsl(120, 43%, 65%);
    margin-right: 1rem;
}

.delete_btn {
    background-color: hsl(0, 44%, 65%);
}

.update_btn:hover,
.delete_btn:hover {
    background-color: hsl(266, 43%, 65%);
    color: black;
}

.details_container {
    display: flex;
    justify-content: space-around;
    height: max-content;
    flex: 0 1;
    gap: 2rem;
    margin-inline: 15rem;
}

.details__image {
    height: 400px;
    object-fit: contain;
}

.details_info {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 1rem;
}

.details__header {
    font-weight: 600;
    font-size: 1.5rem;
    letter-spacing: 1px;
}

.details__date {
    font-weight: 400;
    letter-spacing: 1px;
}

.details__author,
.details__publisher,
.details__category,
.details__language,
.details__pages,
.details__description {
    font-size: 1.2rem;
    letter-spacing: 1px;
}
.details_descContainer {
    margin-block: 3rem;
    margin-inline: 15rem;
    padding: 1rem;
}

.desc__title {
    font-weight: 500;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.details__description {
    padding: 1rem;
    line-height: 3.5ch;
}
